import { useState } from 'react';

import Box from '@mui/material/Box';
import Link from '@mui/material/Link';
import Card from '@mui/material/Card';
import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';
import Divider from '@mui/material/Divider';
import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';
import IconButton from '@mui/material/IconButton';
import LoadingButton from '@mui/lab/LoadingButton';
import { alpha, useTheme } from '@mui/material/styles';
import InputAdornment from '@mui/material/InputAdornment';

import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';

import Dropzone from 'react-dropzone';

import { useRouter } from 'template/routes/hooks';

import { bgGradient } from 'template/theme/css';

import Logo from 'template/components/logo';
import Iconify from 'template/components/iconify';

// ----------------------------------------------------------------------

// core import
import { useSelector, useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import React, { useEffect } from 'react';
import { Grid } from '@mui/material';

import {
  // selectors
  selectAccessToken,

  // actions

  // thunks
} from 'features/user/userSlice';

import {
  // selectors
  selectShopUpdateStatus,
  selectShopError,
  selectShopErrors,
  selectSelectedShop,
  // actions
  setShopErrors,
  setShopUpdateStatus,
  updateSelectedShop,
} from 'features/main/mainSlice';

import {
  // thunks
  updateShopAsyncThunk,
} from 'features/shop/shopSlice';

// ------------------------ //

export default function ShopEditView() {
  const theme = useTheme();

  const router = useRouter();

  const [showPassword, setShowPassword] = useState(false);

  const handleClick = () => {
    router.push('/dashboard');
  };

  // -------- State Mangement ---------- //

  // core
  const navigate = useNavigate();
  const dispatch = useDispatch();

  // internal state
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    password: '',
    confirm_password: '',
  });
  const [files, setFiles] = useState({
    background_image: null,
    shop_image: null,
  });
  // const [errors, setErrors] = useState({}); // Object to store validation errors

  // selector hooks
  // shop
  const status = useSelector(selectShopUpdateStatus);
  const error = useSelector(selectShopError);
  const errors = useSelector(selectShopErrors);
  const shop = useSelector(selectSelectedShop);
  // user
  const access_token = useSelector(selectAccessToken);

  const handleChange = (e) => {
    const { name, value } = e.target;
    console.log({ name, value });
    dispatch(updateSelectedShop({ name, value }));
    setFormData({ ...formData, [name]: value });
    // set shop info
  };

  const handleSubmit = (event) => {
    console.log(event);
    event.preventDefault();
    const validationErrors = {};
    console.log(validationErrors);

    if (Object.keys(validationErrors).length > 0) {
      dispatch(setShopErrors(validationErrors));
    } else {
      dispatch(setShopErrors({}));
      const data = new FormData(event.target);
      if (files['background_image'] !== null) {
        data.append('background_image', files['background_image']);
      }
      if (files['shop_image'] !== null) {
        data.append('shop_image', files['shop_image']);
      }
      console.log(data.get('background_image')); // You can send this data to your server for registration
      console.log(data.get('shop_image'));
      console.log(data.get('access_token'));
      dispatch(updateShopAsyncThunk(data));
    }
  };

  const handleFileChanges = (event) => {
    const { name, current_files } = event;
    console.log('Name & Files:', name, current_files);
    setFiles({ ...files, [name]: current_files.length == 0 ? null : current_files[0] });
  };

  const handleClose = () => {
    dispatch(setShopUpdateStatus('idle'));
  };

  useEffect(() => {
    if (status == 'succeeded') {
      // set back to idle
      dispatch(setShopUpdateStatus('idle'));
      // and then go back to shops
      navigate('/shops');
    }
  }, [status]); // Include history as a dependency

  // -------- UI Section ------------ //

  const renderForm = (
    <>
      <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>
        <input type="hidden" value={access_token} name="access_token" placeholder="Hidden Input" />
        <input type="hidden" name="id" value={shop.id} placeholder="Shop Id" />

        <Grid container spacing={3}>
          <Grid item xs={12} sm={6}>
            <TextField
              name="name"
              label="Shop Name"
              value={shop.name ?? ""}
              onChange={handleChange}
              error={errors.hasOwnProperty('name')}
              helperText={errors.name}
              fullWidth
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              name="description"
              label="Shop Description"
              value={shop.description ?? ""}
              onChange={handleChange}
              error={errors.hasOwnProperty('description')}
              helperText={errors.description}
              fullWidth
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              name="address"
              label="Address"
              value={shop.address ?? ""}
              onChange={handleChange}
              error={errors.hasOwnProperty('address')}
              helperText={errors.address}
              fullWidth
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              name="phone"
              label="Phone Number"
              value={shop.phone ?? ""}
              onChange={handleChange}
              error={errors.hasOwnProperty('phone')}
              helperText={errors.phone}
              fullWidth
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              name="url"
              label="URL"
              value={shop.url ?? ""}
              onChange={handleChange}
              error={errors.hasOwnProperty('url')}
              helperText={errors.url}
              fullWidth
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              name="wifi_name"
              label="Wifi Name"
              value={shop.wifi_name ?? ""}
              onChange={handleChange}
              error={errors.hasOwnProperty('wifi_name')}
              helperText={errors.wifi_name}
              fullWidth
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              name="wifi_password"
              label="Wifi Password"
              value={shop.wifi_password ?? ""}
              onChange={handleChange}
              error={errors.hasOwnProperty('wifi_password')}
              helperText={errors.wifi_password}
              fullWidth
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            {/* intentionally leave blank groid */}
          </Grid>
          <Grid item xs={12} sm={6}>
            <Typography>Shop Image {files['background_image']?.name}</Typography>
            <TextField
              type="file"
              name="shop_image"
              onChange={handleChange}
              error={errors.hasOwnProperty('shop_image')}
              helperText={errors.shop_image}
              fullWidth
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <Typography>Background Image {files['background_image']?.name}</Typography>
            <TextField
              type="file"
              name="background_image"
              onChange={handleChange}
              error={errors.hasOwnProperty('background_image')}
              helperText={errors.background_image}
              fullWidth
            />
          </Grid>
        </Grid>

        <Stack direction="row" alignItems="center" justifyContent="flex-end" sx={{ my: 3 }}>
          <Link variant="subtitle2" underline="hover"></Link>
        </Stack>

        <LoadingButton
          fullWidth
          size="large"
          type="submit"
          variant="contained"
          color="inherit"
          loading={status !== 'idle'}
        >
          Update Shop
        </LoadingButton>
      </Box>
    </>
  );

  const renderDialog = (
    <Dialog
      open={status === 'error'}
      onClose={handleClose}
      aria-labelledby="alert-dialog-title"
      aria-describedby="alert-dialog-description"
    >
      <DialogTitle id="alert-dialog-title">{'Error'}</DialogTitle>
      <DialogContent>
        <DialogContentText id="alert-dialog-description">{error}</DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button onClick={handleClose} autoFocus>
          OK
        </Button>
      </DialogActions>
    </Dialog>
  );

  return (
    <Box
      sx={{
        ...bgGradient({
          color: alpha(theme.palette.background.default, 0.9),
          imgUrl: '/assets/background/overlay_4.jpg',
        }),
        height: 1,
      }}
    >
      <Logo
        sx={{
          position: 'fixed',
          top: { xs: 16, md: 24 },
          left: { xs: 16, md: 24 },
        }}
      />

      <Stack alignItems="center" justifyContent="center" sx={{ height: 1 }}>
        {/* main card ကြီး */}
        <Card
          sx={{
            p: 5,
            width: 1,
            maxWidth: 1024,
          }}
        >
          <Typography variant="h4">New Shop Form</Typography>

          <Typography variant="body2" sx={{ mt: 2, mb: 5 }}>
            Create new shop
          </Typography>

          {renderForm}
        </Card>
      </Stack>

      {renderDialog}
    </Box>
  );
}
